.toggle-button{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none; 
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 2px;
    background: white;
}
.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #1d1d1c;
    height: 60px;
}
.toolbar__navigation {
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 1rem;
}

.toolbar__logo {
    margin-left: -2rem;
    
}

.logo img {
    /* width: 175px; */
   /* border: 2px solid gold; */
    /* vertical-align: middle; */
}

.toolbar__logo a {
    color: white;
    text-decoration: none;
    font-size: 0rem;
}

.spacer1{
    -webkit-flex: 1 1;
            flex: 1 1;
}

/* .spacer{
    flex: 0;
} */

.toolbar_navigation-items li{
    padding: 0 0.5rem;

}

.toolbar_navigation-items a {
    color: white;
    text-decoration: none;
}
.toolbar_navigation-items ul{
    display:  -webkit-flex;
    display:  flex;
    list-style: none;
    margin: 0;
    padding: 0;
    padding: flex;
}

.toolbar_navigation-items ul:hover,
.toolbar_navigation-items ul:active {
    color: #fa9231;
}

@media (max-width: 768px) {
    .toolbar_navigation-items {
        display: none;
    }
    .toolbar__navigation, .toolbar__logo {
        display: -webkit-flex;
        display: flex;
        /* margin-right: 0; */
    }
    .spacer {
        -webkit-flex: 1 1;
                flex: 1 1;
    }

    
}

@media (min-width: 769px) {
    .toolbar__toggle-button {
        display: none;
    }
