.side-drawer {
    height: 100%;
    background-color: rgb(51, 49, 46);
    opacity: 0.97;
    box-shadow: 1px 0 7px rgba(0, 0, 0, 3);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(-101%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open {
    transform: translateX(0);
    background-color: rgb(51, 49, 46);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0
}

.side-drawer a {
    color: #fa923f;
    text-combine-upright: none;
    font-size: 1.2rem;
    text-decoration: none;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: #f7760c;
    font-size: 1.35rem;

}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}